<template>
  <div>
  <el-tabs v-model="activeName" :tab-position="'left'" @tab-click="handleClick" class="tab">
    <el-tab-pane label="概要" name="index">
      <div class="pagecontent">
        <div class="md">
          
          <div class="word">
            <div class="title">二次开发文档说明</div>
            <div class="doc">
              左侧导航栏可以找到全部合适二次开发的接口, 建议只使用主体管理接口和农产品添加删减接口, 剩余的通过登录控制台在后台完成操作
              <p>可以使用我们提供的账户进入控制台进行数据修改和操作</p>
            </div>
          </div>
          
          <div class="word">
            <div class="title">接口请求说明</div>
            <div class="doc">
              首先获取平台提供的请求密钥, 可在微信与(Weivi)取得<br>
              请求时 http报文 headers部分需要携带AuthAccessKey请求密钥<br><br>
              headers{<br>
                <p>&nbsp;&nbsp;&nbsp;['AuthAccessKey'] = AuthAccessKey</p>
              }<br><br><br>
              在Vue中的使用方法<br>
              定义 service.interceptors.request.use() 在obj对象中添加 headers['AuthAccessKey'] = 密钥
              <p>若返回10086 代表请求体参数有误或</p>
            </div>
          </div>

          <div class="word">
            <div class="title">数据结构说明</div>
            <div class="doc">左侧导航栏可以找到全部合适二次开发的接口</div>
            <img src="@/assets/docs/1.png" />
          </div>

        </div>
      </div>
    </el-tab-pane>
    <el-tab-pane label="农产品分类管理接口" name="1">
      <div class="pagecontent"><div class="docbody"><div class="content"><ApisItem :data=item :sty="0" v-for="(item, index) in docdata[1]" :key="'1' + index"/></div></div></div>
    </el-tab-pane>
    <el-tab-pane label="主体管理接口" name="2">
      <div class="notapi" v-if="docdata[2].length == 0">文档正在陆续上线中</div>
      <div class="pagecontent"><div class="docbody"><div class="content"><ApisItem :data=item :sty="0" v-for="(item, index) in docdata[2]" :key="'2' + index"/></div></div></div>
    </el-tab-pane>
    <el-tab-pane label="主题下农产品管理接口" name="3">
    <div class="notapi" v-if="docdata[3].length == 0">文档正在陆续上线中</div>
      <div class="pagecontent"><div class="docbody"><div class="content"><ApisItem :data=item :sty="0" v-for="(item, index) in docdata[3]" :key="'3' + index"/></div></div></div>
    </el-tab-pane>
    <el-tab-pane label="采集数据调取接口" name="4">
    <div class="notapi" v-if="docdata[4].length == 0">文档正在陆续上线中</div>
      <div class="pagecontent"><div class="docbody"><div class="content"><ApisItem :data=item :sty="0" v-for="(item, index) in docdata[4]" :key="'4' + index"/></div></div></div>
    </el-tab-pane>
    <el-tab-pane label="设备从机和指令参数接口" name="5">
    <div class="notapi" v-if="docdata[5].length == 0">文档正在陆续上线中</div>
      <div class="pagecontent"><div class="docbody"><div class="content"><ApisItem :data=item :sty="0" v-for="(item, index) in docdata[5]" :key="'5' + index"/></div></div></div>
    </el-tab-pane>
    <el-tab-pane label="网关设备接口" name="6">
    <div class="notapi" v-if="docdata[6].length == 0">文档正在陆续上线中</div>
      <div class="pagecontent"><div class="docbody"><div class="content"><ApisItem :data=item :sty="0" v-for="(item, index) in docdata[6]" :key="'6' + index"/></div></div></div>
    </el-tab-pane>
    <el-tab-pane label="设备回调参数模板管理接口" name="7">
    <div class="notapi" v-if="docdata[7].length == 0">文档正在陆续上线中</div>
      <div class="pagecontent"><div class="docbody"><div class="content"><ApisItem :data=item :sty="0" v-for="(item, index) in docdata[7]" :key="'7' + index"/></div></div></div>
    </el-tab-pane>
  </el-tabs>
  </div>
</template>

<script>
// @ is an alias to /src
import { List } from "@/api/opendosc";
import ApisItem from "@/components/ApisItem.vue";
export default {
  name: "Home",
  data() {
    return {
      activeName: 'index',
      docdata:{
        1: [],
        2: [],
        3: [],
        4: [],
        5: [],
        6: [],
        7: [],
        8: []
      }
    };
  },
  components: {
    ApisItem
  },
  methods: {
    handleClick(tab) {
      if (tab.name != 'index'){
        if (tab.index){
          if(this.docdata[tab.index].length == 0){
            console.log("加载",tab.index)
            this.$http(
              List({
                type: tab.index
              }),
              res => {
                if (res.code == 200) {
                  // this.$message({
                  //   message: res.msg,
                  //   type: "success"
                  // });
                  this.docdata[tab.index] = res.data;
                } else {
                  this.$message({
                    message: res.msg,
                    type: "error",
                    duration: 5 * 1000
                  });
                }
              }
            );
          }else{
            console.log("已经加载过",tab.index)
          }
        }
      }
    }
  },
  created() {
  }
};
</script>
